import React from "react";
import Input from "@ezycore/ui/components/molecules/Fields/Input";

export interface SearchProps {}

function Search({}: SearchProps) {
  const deepMatchFiltered = (obj: object, regex: string) => {
    if (typeof obj === "string" || typeof obj === "number") {
      return regex.test(String(obj));
    }

    if (Array.isArray(obj)) {
      return obj.some((item) => deepMatchFiltered(item, regex));
    }

    if (typeof obj === "object" && obj !== null) {
      return Object.values(obj).some((value) =>
        deepMatchFiltered(value, regex)
      );
    }

    return false;
  };

  const search = (query, props) => {
    const regex = new RegExp(query, "i");

    const newArr = families.filter((family) =>
      props.some((prop) => deepMatchFiltered(family[prop], regex))
    );
    setFamiliesToShow(newArr);
  };

  const handleSearch = (e) => {
    const query = e.target.value;
    const props = [
      "familyName",
      "firstname",
      "lastname",
      "parents",
      "students",
      "name",
    ];
    search(query, props);
  };

  return (
    <div className="search mb-8">
      <Input
        type="text"
        label="Recherche..."
        icon="search"
        placeholder="Nom, Prenom..."
        onChange={handleSearch}
      />
    </div>
  );
}

export default Search;
